import React from "react";
import classNames from "classnames";
import Icon from "../../Icon";
import { fire } from "../../../images";

import style from './styles/sizesitems.module.scss';

const SizesItems = ({
  isLoadingDataProduct,
  handleChooseProduct = () => {},
  in_stock_count,
  collection,
  productId,
  colors,
  sizes,
}) => {

  return (
    <div className={style['prodpage-sizes']}>
      <ul className={style['prodpage-sizes__items']}>
        {
          sizes?.length?
          sizes.map((el) => {
          
            return (
              <li
                key={`element-color-${el.id}`}
                className={classNames({
                    [style['prodpage-sizes__item']]:true,
                    [style['prodpage-sizes__item--disable']]:isLoadingDataProduct,
                  })
                }
              >
                <div
                  // disabled={el.selected}
                  disabled={!isLoadingDataProduct}
                  className={classNames({
                    [style['prodpage-sizes__btn']]: true,
                    [style['prodpage-sizes__btn--active']]: el.selected,
                    [style['prodpage-sizes__btn--redeemed']]: el?.redeemed,
                    // [style['prodpage-sizes__btn--disabled']]: !!!productId || disabledButton,
                    [style['prodpage-sizes__btn--disabled']]: isLoadingDataProduct,

                  })}
                  onClick={() => {
                    if(!collection) handleChooseProduct(productId, colors, el.id)

                  }}
                  variant="text"
                >
                  {el.title}
                </div>
              </li>
            );
          })
          : null
        }
      </ul>
      {
        in_stock_count ? (
          <p className={style['prodpage-sizes__remainder']}>
            <Icon className={style['prodpage-sizes__remainder-btn']} src={fire} width={20} height={20} />
            Осталось:{in_stock_count} ед.
          </p>
          ) : null
      }
    </div>
  )
}

export default SizesItems;